@keyframes loaderCodeAnimation {
    0% {
        transform: translateY(100%);
    }

    100% {
        transform: translateY(-110%);
    }
}

@keyframes loaderInitiatingAnimation {
    0% {
        opacity: 1;

    }

    100% {
        opacity: 0;
    }
}

.loader {
    margin: 0;
    width: 100vw;
    height: 100vh;
}

.loaderCodeWrapper {
    width: 100vw;
    height: 100vh;
    position: absolute;
    overflow-y: hidden;
}

.loaderCode {
    margin: 0;
    font-family: monofonto;
    font-weight: 300;
    color: #ffffff;
    letter-spacing: 0.1vw;
    line-height: 1.85vw;
    font-size: 1.5vw;
    width: 70vw;
    margin-left: 15vw;
    position: absolute;
    animation-name: loaderCodeAnimation;
    animation-duration: 2.25s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
}

.loaderInitiatingWrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.loaderInitiating {
    margin: 0;
    font-family: monofonto;
    font-weight: 300;
    color: #ffffff;
    text-transform: uppercase;
    letter-spacing: 0.05vw;
    font-size: 2.75vw;
    opacity: 0;
    animation-name: loaderInitiatingAnimation;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    animation-direction: alternate;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}

.loaderInitiatingWrapper svg {
    width: 15vw;
    margin-bottom: 1vw;
}

.loaderInitiatingNoShow {
    color: #000000;
}

@media only screen and (max-width: 850px) {
    .loaderCode {
        letter-spacing: 0.125vw;
        line-height: 2.35vw;
        font-size: 2vw;
    }

    .loaderInitiating {
        letter-spacing: 0.075vw;
        font-size: 3.5vw;
    }

    .loaderInitiatingWrapper svg {
        width: 20vw;
        margin-bottom: 1vw;
    }


}

@media only screen and (max-width: 650px) {
    .loaderCode {
        letter-spacing: 0.2vw;
        line-height: 3.5vw;
        font-size: 3.5vw;
    }

    .loaderInitiating {
        letter-spacing: 0.1vw;
        font-size: 6vw;
    }
    
    .loaderInitiatingWrapper svg {
        width: 25vw;
        margin-bottom: 1vw;
    }
}
