.footerWrapper {
    width: 100vw;
    height: 100vh;
}

.footer {
    position: absolute;
    width: 90vw;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.footerTitle {
    margin: 0;
    color: #ffffff;
    font-family: robotoLite;
    font-size: 2vw;
    letter-spacing: 0.25vw;
    text-transform: uppercase;
    margin-bottom: 5vw;
}

.footerLink {
    margin: 0;
    color: #ffffff;
    text-decoration: none;
    font-family: roboto;
    font-size: 5vw;
    letter-spacing: 0.25vw;
}

.footerBar {
    text-align: center;
}

.footerBar svg {
    width: 10vw;
    height: auto;
    position: absolute;
    top: 95%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@media only screen and (max-width: 850px) {
    .footerTitle {
        font-size: 2vw;
        letter-spacing: 0.35vw;
        margin-bottom: 6vw;
    }

    .footerLink {
        font-size: 6.5vw;
        letter-spacing: 0.45vw;
    }

    .footerBar svg {
        width: 20vw;
        top: 93.5%;
    }
}

@media only screen and (max-width: 650px) {
    .footerTitle {
        font-size: 3vw;
        letter-spacing: 0.35vw;
        margin-bottom: 8vw;
    }

    .footerLink {
        font-size: 6.5vw;
        letter-spacing: 0.45vw;
    }
    
    .footerBar svg {
        width: 22.5vw;
        top: 95%;
    }
}
