@keyframes mainRollingAnimation {
    0% {
        margin-top: -30vw;
    }

    15% {
        margin-top: 0vw;
    }

    30% {
        margin-top: 30vw;
    }

    31% {
        margin-top: -30vw;
    }

    46% {
        margin-top: 0vw;
    }

    61% {
        margin-top: 30vw;
    }

    62% {
        margin-top: -20vw;
    }

    77% {
        margin-top: 0vw;
    }

    92% {
        margin-top: 20vw;
    }

    100% {
        margin-top: 0;
    }
}

@keyframes lazyScrollAnimation {
    0% {
        margin-top: 250px;
        opacity: 0;
    }
    
    100% {
        margin-top: 0;
        opacity: 1;
    }
}

@keyframes darkAnimation {
    0% {
        opacity: 1;
    }
    
    100% {
        opacity: 0;
    }
}

.main {
    margin: 0;
    width: 100vw;
    height: 100vh;
    position: relative;
    animation-name: mainRollingAnimation;
    animation-duration: 0.25s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
}

.mainHeader {
    position: absolute;
    z-index: 100;
    top: 50%;
    right: -4.5vw;
    -ms-transform: translateY(-50%) rotate(-90deg);
    transform: translateY(-50%) rotate(-90deg);
}

.mainHeaderLink {
    margin: 0;
    font-family: robotoReg;
    color: #ffffff;
    text-decoration: none;
    font-size: 1vw;
    letter-spacing: 0.15vw;
    opacity: 0.5;
    -webkit-transition: all .2s linear;
    -moz-transition: all .2s linear;
    -o-transition: all .2s linear;
    transition: all .2s linear;
}

.mainHeaderLink:hover {
    opacity: 1;
}

.mainContentFront {
    position: relative;
    width: 100vw;
    height: 100vh;
}

.mainParticlesBg {
    position: absolute;
    width: 100vw;
    height: 100vh;
    top: 0;
}

.mainContentFrontTexts {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.mainContentFrontTextsTitle {
    margin: 0;
    width: 90vw;
    font-family: ailerons;
    color: #ffffff;
    font-size: 6vw;
    letter-spacing: 1vw;
}

.mainContentFrontTextsSubtitle {
    margin: 0;
    margin-top: 2vw;
    font-family: robotoLite;
    color: #ffffff;
    font-size: 1.5vw;
    letter-spacing: 0.75vw;
    text-transform: uppercase;
}

.mainContentContentWrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.mainContentBody {
    width: 100vw;
    height: 100vh;
}

.mainIconParticles {
    width: 25vw !important;
    height: 30vw !important;
    padding-right: 3vw;
}

.mainIconTexts {
    color: #ffffff;
    width: 25vw;
    padding-left: 3vw;
    border-left: 0.1vw solid #ffffff;
}

.mainIconTextsName {
    margin: 0;
    font-family: ailerons;
    font-size: 3.75vw;
    letter-spacing: -0.25vw;
    line-height: 4.5vw;
}

.mainIconTextsDesc {
    margin: 0;
    margin-top: 2vw;
    margin-bottom: 2vw;
    font-family: roboto;
    font-size: 1.25vw;
    letter-spacing: 0.25vw;
}

.mainIconTextsImg {
    width: 25vw;
    margin-bottom: 0.75vw;
}

.mainCodeWrapper {
    position: absolute;
    bottom: 0;
    width: 100vw;
    z-index: 100;
    background-color: #ffffff;
}

.mainCode {
    margin: 0;
    font-family: VT323;
    font-size: 2vw;
    letter-spacing: 0.1vw;
    margin-top: 0.25vw;
    margin-bottom: 0.25vw;
}

.mainPiecePageWrapper {
    position: fixed;
    z-index: 99;
    width: 100vw;
    height: 100vh;
    background-color: #000000;
    overflow-y: auto;
    animation-name: mainRollingAnimation;
    animation-duration: 0.25s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
}

.mainPiecePageBack {
    position: fixed;
    margin: 0;
    color: #ffffff;
    font-family: robotoReg;
    font-size: 2vw;
    letter-spacing: 0.1vw;
    top: 5vw;
    left: 3vw;
    cursor: pointer;
}

.mainPiecePageBack svg {
    width: 2.5vw;
    height: auto;
    margin-bottom: -0.5vw;
    margin-right: 1vw;
}

.mainPiecePage {
    width: 70vw;
    margin: auto;
    margin-top: 10vw;
    margin-bottom: 13vw;
}

.mainPiecePageTitle {
    margin: 0;
    font-family: ailerons;
    color: #ffffff;
    font-size: 10vw;
    text-align: left;
    display: table-caption;
    line-height: 12vw;
    letter-spacing: 0.4vw;
}

.mainPiecePageSub {
    margin: 0;
    margin-top: 3vw;
    margin-bottom: 3vw;
    margin-left: 1vw;
    color: #ffffff;
    font-family: robotoLite;
    font-size: 1.5vw;
    letter-spacing: 0.3vw;
}

.mainPiecePageImg {
    width: 100%;
    margin: 0;
}

.mainPiecePageParagraph {
    width: 45vw;
    margin: auto;
    font-family: roboto;
    font-size: 1.25vw;
    color: #ffffff;
    letter-spacing: 0.15vw;
    line-height: 2vw;
    margin-top: 11vw;
    margin-bottom: 11vw;
}

.mainPiecePageLine {
    width: 5vw;
    margin: auto;
    border-bottom: 0.4vw solid #ffffff;
    margin-bottom: 10vw;
}

.mainPiecePageLinkWeb,
.mainPiecePageLinkGit {
    text-align: center;
}

.mainPiecePageLinkWeb a,
.mainPiecePageLinkGit a {
    font-family: roboto;
    color: #ffffff;
    font-size: 1.5vw;
    letter-spacing: 0.25vw;
    text-decoration: none;
    padding-top: 1vw;
    padding-bottom: 1vw;
    padding-left: 3vw;
    padding-right: 3vw;
    border: 0.1vw solid #ffffff;
    -webkit-transition: all .2s linear;
    -moz-transition: all .2s linear;
    -o-transition: all .2s linear;
    transition: all .2s linear;
}

.mainPiecePageLinkWeb a:hover,
.mainPiecePageLinkGit a:hover {
    background-color: #ffffff;
    color: #000000;
}

.mainPiecePageLinkGit {
    margin-top: 7vw;
}

.mainPiecePageSingle {
    animation-name: lazyScrollAnimation;
    animation-duration: 0.25s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in;
}

.goingDark {
    animation-name: darkAnimation;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-in;
}

@media only screen and (max-width: 850px) {
    .mainHeader {
        right: -6.5vw;
    }

    .mainHeaderLink {
        font-size: 1.5vw;
        letter-spacing: 0.2vw;
    }

    .mainContentFrontTextsTitle {
        font-size: 8vw;
        letter-spacing: 1vw;
    }

    .mainContentFrontTextsSubtitle {
        margin-top: 4.5vw;
        font-size: 2vw;
        letter-spacing: 1vw;
    }

    .mainIconParticles {
        width: 37.5vw;
        height: 45vw;
        padding-right: 3vw;
    }

    .mainIconTexts {
        width: 37vw;
        padding-left: 3vw;
    }

    .mainIconTextsName {
        font-size: 6vw;
        letter-spacing: -0.25vw;
        line-height: 8vw;
    }

    .mainIconTextsDesc {
        margin-top: 3vw;
        margin-bottom: 3vw;
        font-size: 2vw;
        letter-spacing: 0.4vw;
    }

    .mainIconTextsImg {
        width: 37.5vw;
        margin-bottom: 1.25vw;
    }

    .mainPiecePage {
        width: 70vw;
        margin-top: 25vw;
        margin-bottom: 20vw;
    }

    .mainPiecePageTitle {
        font-size: 15vw;
        line-height: 17vw;
        letter-spacing: 0.5vw;
    }

    .mainPiecePageSub {
        margin-top: 6vw;
        margin-bottom: 6vw;
        margin-left: 1.5vw;
        font-size: 3vw;
        letter-spacing: 0.5vw;
    }

    .mainPiecePageLine {
        border-bottom: 0.4vw solid #ffffff;
        margin-bottom: 16vw;
    }

    .mainPiecePageParagraph {
        width: 60vw;
        font-size: 2vw;
        letter-spacing: 0.2vw;
        line-height: 4vw;
        margin-top: 15vw;
        margin-bottom: 15vw;
    }

    .mainPiecePageLinkWeb a,
    .mainPiecePageLinkGit a {
        font-size: 2.25vw;
        letter-spacing: 0.3vw;
        padding-top: 1.5vw;
        padding-bottom: 1.5vw;
        padding-left: 5vw;
        padding-right: 5vw;
    }

    .mainPiecePageLinkGit {
        margin-top: 12vw;
    }
    
    .mainPiecePageBack {
        font-size: 3vw;
        letter-spacing: 0.2vw;
        top: 7vw;
        left: 2vw;
    }

    .mainPiecePageBack svg {
        width: 3.5vw;
        margin-right: 1vw;
    }
}

@media only screen and (max-width: 650px) {
    .mainHeader {
        right: -7.5vw;
    }

    .mainHeaderLink {
        font-size: 1.75vw;
        letter-spacing: 0.225vw;
    }

    .mainContentFrontTextsTitle {
        font-size: 10vw;
        letter-spacing: 1vw;
    }

    .mainContentFrontTextsSubtitle {
        margin-top: 5vw;
        font-size: 2.5vw;
        letter-spacing: 1vw;
    }

    .mainIconParticles {
        width: 37.5vw;
        height: 45vw;
        padding-right: 4vw;
    }

    .mainIconTexts {
        width: 37.5vw;
        padding-left: 4vw;
    }

    .mainIconTextsName {
        font-size: 6.5vw;
        letter-spacing: -0.25vw;
        line-height: 8vw;
    }

    .mainIconTextsDesc {
        margin-top: 4vw;
        margin-bottom: 4vw;
        font-size: 2vw;
        letter-spacing: 0.4vw;
    }

    .mainIconTextsImg {
        width: 37.5vw;
        margin-bottom: 1.5vw;
    }

    .mainPiecePage {
        width: 75vw;
        margin-top: 25vw;
        margin-bottom: 20vw;
    }

    .mainPiecePageTitle {
        font-size: 15vw;
        line-height: 17vw;
        letter-spacing: 0.5vw;
    }

    .mainPiecePageSub {
        margin-top: 7vw;
        margin-bottom: 7vw;
        margin-left: 1.5vw;
        font-size: 3vw;
        letter-spacing: 0.5vw;
    }

    .mainPiecePageLine {
        border-bottom: 0.4vw solid #ffffff;
        margin-bottom: 18vw;
    }

    .mainPiecePageParagraph {
        width: 60vw;
        font-size: 3vw;
        letter-spacing: 0.3vw;
        line-height: 5vw;
        margin-top: 20vw;
        margin-bottom: 20vw;
    }

    .mainPiecePageLinkWeb a,
    .mainPiecePageLinkGit a {
        font-size: 3.5vw;
        letter-spacing: 0.35vw;
        padding-top: 2vw;
        padding-bottom: 2vw;
        padding-left: 6vw;
        padding-right: 6vw;
    }

    .mainPiecePageLinkGit {
        margin-top: 13vw;
    }

    .mainPiecePageBack {
        font-size: 3vw;
        letter-spacing: 0.2vw;
        top: 6vw;
        left: 2vw;
    }

    .mainPiecePageBack svg {
        width: 3.5vw;
        margin-right: 1vw;
    }
}
