@font-face {
    font-family: monofonto;
    src: local('monofonto'), url(../Fonts/monofonto.ttf) format('truetype');
}

@font-face {
    font-family: robotoReg;
    src: local('robotoReg'), url(../Fonts/RobotoCondensed-Regular.ttf) format('truetype');
}

@font-face {
    font-family: robotoLite;
    src: local('robotoLite'), url(../Fonts/RobotoCondensed-Light.ttf) format('truetype');
}

@font-face {
    font-family: roboto;
    src: local('roboto'), url(../Fonts/Roboto-Thin.ttf) format('truetype');
}

@font-face {
    font-family: ailerons;
    src: local('ailerons'), url(../Fonts/Ailerons.otf) format('opentype');
}

@font-face {
    font-family: VT323;
    src: local('VT323'), url(../Fonts/VT323-Regular.ttf) format('truetype');
}

html {
    background-color: #000000;
}

.App {
    background-color: #000000;
}

.App::before {
    content: " ";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: linear-gradient(rgba(0, 0, 0, 0) 50%, rgba(0, 0, 0, 0.1) 50%);
    z-index: 666;
    background-size: 100% 3px, 4px 100%;
    pointer-events: none;
}
